@import '~antd/dist/antd.css';

$customRed: #ff0000;
$customOrange: #f1a400;
$customGreen: #14b759;
$customPurple: #8957e5;
$customGray: #ddd;
$customBlue: #388bfd;
$customDark: #202932;

.App {
  text-align: center;
}

.App-logo {
  height: 200px;
  pointer-events: none;
}

.toggleTheme {
  position: absolute;
  top:0;
  right:0;
  padding: 0 5px;
  .ant-switch {
    &.ant-switch-checked {
      background: #f1a400;
    }
  }
}

.not-found-wrapper {
  color: #ccc;
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 150px);
  .not-found {
    flex:1;
  }
  .title {
    line-height: 60px;
    font-size: 1.4em;
    color: #888;
  }
}

.darkMode {
  .not-found-wrapper {
    color: #888;
  }
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content-wrapper {
  width: 100vw;
  margin:auto;
}

.mainBanner {
  width: 100%;
  height: 300px;
  background: url("./img/snowman.jpg") no-repeat center;
  background-size: cover;
  background-attachment: fixed;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.profile-color-modes-illu-group {
  stroke: $customGray;
}
.profile-color-modes-illu-frame {
  opacity: 0;
}
.profile-color-modes-illu-frame:nth-child(8) {
  animation: profile-light-color-modes-illu-anim-frame-show 0s forwards,profile-light-color-modes-illu-anim-frame-hide 0s .3s forwards;
}
.profile-color-modes-illu-frame:nth-child(9) {
  animation: profile-light-color-modes-illu-anim-frame-show 0s .3s forwards,profile-light-color-modes-illu-anim-frame-hide 0s .36s forwards;
}
.profile-color-modes-illu-frame:nth-child(10) {
  animation: profile-light-color-modes-illu-anim-frame-show 0s .36s forwards;
}

.darkMode {
  .profile-color-modes-illu-group {
    animation: profile-color-modes-illu-anim .2s cubic-bezier(.72,.08,1,.68) backwards
  }
  .profile-color-modes-illu-green {
    stroke: $customGreen;
    animation-delay: .8s;
    animation-duration: .2s;
    animation-timing-function: cubic-bezier(.47,2.92,.84,-1.5);
  }
  .profile-color-modes-illu-red {
    stroke: $customRed;
    animation-delay: .4s;
    animation-duration: .1s;
  }
  .profile-color-modes-illu-orange {
    stroke: $customOrange;
    animation-delay: .5s;
    animation-duration: .1s;
  }
  .profile-color-modes-illu-purple {
    stroke: $customPurple;
    animation-delay: .6s;
    animation-duration: .1s;
  }
  .profile-color-modes-illu-blue {
    stroke: $customBlue;
    animation-delay: .7s;
    animation-duration: .1s;
  }
  .profile-color-modes-illu-frame {
    animation: profile-color-modes-illu-anim-frame-show 0s forwards,profile-color-modes-illu-anim-frame-hide 0s forwards;
    &:first-child {
      opacity: 1;
      animation: profile-color-modes-illu-anim-frame-hide 0s forwards;
      animation-delay: 1.8s;
    }
    &:nth-child(2) {
      animation-delay: 1.8s,2.58s;
    }

    &:nth-child(3) {
      animation-delay: 2.58s,2.66s;
    }

    &:nth-child(4) {
      animation-delay: 2.66s,2.78s;
    }

    &:nth-child(5) {
      animation-delay: 2.78s,2.84s;
    }

    &:nth-child(6) {
      animation-delay: 2.84s,3.44s;
    }

    &:nth-child(7) {
      animation-delay: 3.44s,3.56s;
    }
    &:nth-child(8) {
      animation:profile-color-modes-illu-anim-frame-show 0s forwards;
      animation-delay: 3.56s;
    }
    &:nth-child(9),
    &:nth-child(10) {
      animation: none;
    }
  }
}

@keyframes profile-color-modes-illu-anim {
  0% {
    stroke: $customGray;
  }
}

@keyframes profile-light-color-modes-illu-anim-frame-show {
  0% {
    opacity: 0;
    animation-timing-function: ease-out
  }

  to {
    opacity: 1
  }
}

@keyframes profile-light-color-modes-illu-anim-frame-hide {
  0% {
    opacity: 1;
    animation-timing-function: ease-in
  }

  to {
    opacity: 0
  }
}

@keyframes profile-color-modes-illu-anim-frame-show {
  0% {
    opacity: 0;
    animation-timing-function: ease-out
  }

  to {
    opacity: 1
  }
}

@keyframes profile-color-modes-illu-anim-frame-hide {
  0% {
    opacity: 1;
    animation-timing-function: ease-in
  }

  to {
    opacity: 0
  }
}

@import 'Antd.custom.scss';


